
@font-face {
  font-family: 'B_nazanin';
  font-weight: 500;
  font-style: normal;
  src: url('./assets/fonts/BNazanin.ttf') format('truetype');
  font-display: swap;
}

@font-face {
  font-family: 'B_nazanin_bold';
  font-weight: 500;
  font-style: normal;
  src: url('./assets/fonts/B Nazanin Bold_YasDL.com.ttf') format('truetype');
  font-display: swap;
}

* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

:root {
    --color-white: #fff;
    --color-gray: #999;
    --color-black: #000;
    --color-light-black: #262626;
    --color-green: #20c997;
    --transition: all 800ms ease;
    --font-BNazanin:'B_nazanin';
}

html {
    scroll-behavior: smooth;
}

::-webkit-scrollbar {
    /* display: none; */
}

body {
    font-family: var(--font-BNazanin);
    background-color: var(--color-black) !important;
    color: var(--color-gray);
    overflow-x: hidden;
}


/* ============ GENERAL STYLES =============  */
h1,
h2,
h3,
h4,
h5 {
  font-family: "B_nazanin_bold";
  line-height: 1.2;
  color: var(--color-white);
}

.h1 {
    font-size: 2.5rem;
}

.h2 {
  font-size: 2rem;
}

.h3 {
  font-size: 1.5rem ;
}

.h4 {
  font-size: 1.3rem ;
}

li::selection {
  color: var(--color-white); 
}

button {
  cursor: pointer;
}

img {
  object-fit: cover;
}

.imgContain {
  object-fit: contain;
}

.imgGrayScale {
  -webkit-filter: grayscale(100%); /* Safari 6.0 - 9.0 */
  filter: grayscale(100%);
  transition: var(--transition);
}

.imgGrayScale:hover {
  -webkit-filter: grayscale(0); /* Safari 6.0 - 9.0 */
  filter: grayscale(0);
}

.txtBody {
  color: var(--color-gray);
  font-size: 1.2rem;
  line-height: 1.5rem;
}

.txtBody::selection {
  color: var(--color-white); 
}

.txtError {
  color: #ff3434;
  font-size: .8rem;
}

.h1 {
  font-size: 40px;
  line-height: 44px;
  letter-spacing: 1.5px;
  font-weight: 700;
}

.txtGreen {
  color: var(--color-green);
}

.txtWhite {
  color: var(--color-white);
}

.txtGray {
  color:var(--color-gray);
  font-size: 13px;
  line-height: 25px;
  letter-spacing: 2px;
  font-weight: 500;
}

.txtGray::selection {
  color: var(--color-white); 
}

.btn_black {
  width: max-content;
  max-height: 5rem;
  color: var(--color-white);
  background-color: var(--color-black);
  padding: 1rem 2rem;
  border: 2px solid transparent;
}

.btn_black:hover {
  background-color: var(--color-white);
  border: 2px solid var(--color-black);
  color: var(--color-black);
}

.btn_transparent {
  width: max-content;
  color: var(--color-white);
  background-color: transparent;
  padding: .4rem 3rem;
  border: 2px solid var(--color-white);
}

.btn_transparent:hover {
  background-color: var(--color-white);
  color: var(--color-black);
}

.btn_green {
  width: max-content;
  color: var(--color-green);
  background-color: transparent;
  padding: .4rem 3rem;
  border: 2px solid var(--color-green);
}

.btn_green:hover {
  background-color: var(--color-green);
  color: var(--color-white);
}

.opacity_25 {
  opacity: .25;
}

