/* ------------------------- footer ------------------------- */
#footer {
    width: 100%;
    margin: 2rem 0;
}
/* ------------------------- nav ------------------------- */
nav {
    padding: 1.8rem 3rem;
}

.nav_a {
    font-family: "B_nazanin_bold";
    padding: 10px;
    font-size: 1rem;
    color:var(--color-gray);
}

.nav_a:hover {
    color:var(--color-green);
    border-bottom:1px solid var(--color-green);
}

.nav_logo {
    width: 20%;
}

.nav_social {
    width: 20%;
}

.nav_cardLink {
    width: 60%;
}

.nav_cardDrawer {
    display: none;
}

.cardIcon {
    width: 20%;
}

.nav_logo_svg {
    width: 1.1rem;
    height: 1.1rem;
    color: var(--color-gray);
}

.nav_logo_svg:hover {
    color: var(--color-green);
}

.nav_cardIcon {
    padding: 1rem;
    position: relative;
}

.nav_cardIcon > div {
    width: 20px;
    height: 20px;
    background-color: red;
    border-radius: 10px;
    position: absolute;
    top: 6px;
    right: 6px;
    opacity: .9;
}

.navDrawer {
    position: fixed;
    width: 100vw;
    height: 100vh;
    z-index: 1000;
    left: 0;
    top: 0;
    background-color: rgba(0,0,0,0.8);
}

.navDrawerLeft {
    height: 100%;
    width: 22rem;
    background-color: var(--color-black);
    border-right: 2px solid var(--color-white);
    /* animation: openDrawer_Animation 200ms ; */
}

.navDrawerLeft > * {
    /* animation: opacity_Animation 1000ms ; */
}

.navDrawerRight {
    width: 100%;
    height: 100%;
    z-index: 900;
}

@media screen and (max-width: 900px) {
    nav {
        padding: .5rem 5rem;
    }

    .nav_social, .nav_cardLink {
        display: none;
    }

    .nav_logo {
        width: 100%;
    }

    .nav_cardIcon:hover {
        opacity: .5;
    }

    .nav_cardDrawer {
        display: block;
    }

    .nav_cardDrawer:hover {
        opacity: .5;
    } 

    .navDrawerLeft {
        margin-right: 0;
    }
}

@media screen and (max-width: 600px) {
    nav {
        padding: 1rem 2rem;
    }
}

/* ------------------------- Header Body ------------------------- */
.mainCard {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: flex-end;
    justify-content: space-around;
    flex-wrap: wrap;
    flex-direction: row-reverse;
}

.mainCardItems {
    width: 30.5vw;
    height: 30.5vw;
    position: relative;
    margin-bottom: 2rem;
    background: var(--color-light-black);
    box-shadow: 0px 0px 1px white;
    overflow: hidden;
}

.mainCardItems a {
    background-repeat: no-repeat;
    background-size: cover;
    -webkit-filter: grayscale(100%); /* Safari 6.0 - 9.0 */
    filter: grayscale(100%);
    position: absolute;
    transition: var(--transition);
}

.mainCardItems a:hover {
    -webkit-filter: grayscale(40%); /* Safari 6.0 - 9.0 */
    filter: grayscale(30%);
    scale: 1.03;
}

.mainCardItems h2 {
    text-shadow: 0 0 1px white;
}

@media screen and (max-width: 800px) {
    .mainCardItems {
        width: 47vw;
        height: 47vw;
        margin-bottom: 1rem;
    }
}

@media screen and (max-width: 600px) {
    .mainCardItems {
        width: 90vw;
        height: 60vw;
        margin-bottom: 1rem;
    }
}

/* ------------------------- Proj Cards ------------------------- */
.PCBox {
    width: 80%;
    height: 30rem;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    margin: 4rem 5rem;
    overflow: hidden;
}

.PCBoxImg {
    width: 50%;
    height: 100%;
    background-color: var(--color-light-black);
    overflow: hidden;
}

.PCBoxImg img {
    width: 100%;
    height: 100%;
    -webkit-filter: grayscale(100%); /* Safari 6.0 - 9.0 */
    filter: grayscale(100%);
    transition: var(--transition);
}

.PCBoxImg img:hover {
    -webkit-filter: grayscale(0); /* Safari 6.0 - 9.0 */
    filter: grayscale(0);
}

.PCBoxBody {
    width: 50%;
    height: 100%;
    padding: 0 2.5rem;
    display: flex;
    gap: 1rem;
    align-items: flex-end;
    justify-content: flex-start;
    flex-direction: column;
}

.PCBoxBody h3, .PCBoxBody p {
    text-align: right;
}

.PCBoxBody p {
    padding-right: .5rem;
}

@media screen and (max-width: 1024px) {
    .PCBox {
        height: max-content;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        margin: 4rem 0;
        gap: 2.5rem;
    }

    .PCBoxImg {
        width: 98%;
        height: 50vw;
    }

    .PCBoxBody {
        width: 85%;
        padding: 0;
    }
}

@media screen and (max-width: 600px) {
    .PCBoxBody {
        width: 98%;
        padding: 0;
    }
}


/* ------------------------- Article List ------------------------- */
.cv .CVcontainer {
    width: 95%;
}

.cv .cv_item {
    padding: 0 20px 0 20px;
    margin: 2.5rem;
    position: relative;
    width: 100%;
}

.cv .cv_item_en {
    padding: 0 20px 0 20px;
    margin: 2.5rem 0;
    position: relative;
    width: 100%;
}

.cv .cv_item h3, .cv_item_en h3 {
    color: var(--color-green);
    margin-bottom: 20px;
}

.cv .cv_item h5, .cv_item_en h5 {
    font-size: 16px;
    background: transparent;
    padding: 5px 15px;
    display: inline-block;
    font-weight: 600;
    margin-bottom: 10px;
}

.cv .cv_item ul, .cv_item_en ul {
    padding-left: 20px;
}

.cv .cv_item ul li {
    padding-bottom: 1rem;
    margin-right: 1rem;
    /* list-style: disc; */
}

.cv .cv_item_en ul li {
    padding-bottom: 1rem;
    margin-left: 1.5rem;
}

.cv .cv_item::before {
    content: "";
    position: absolute;
    width: 16px;
    height: 16px;
    border-radius: 50px;
    right: -9px;
    top: 0px;
    background: transparent;
    border: 2px solid var(--color-green);
}

.cv .cv_item::after {
    content: "";
    position: absolute;
    width: 22px;
    height: -webkit-fill-available;
    top: 30px;
    right: 0;
    margin-top: -2px;
    border-right: 2px solid var(--color-green);
}


.cv .cv_item_en::before {
    content: "";
    position: absolute;
    width: 16px;
    height: 16px;
    border-radius: 50px;
    left: 13px;
    top: 0px;
    background: transparent;
    border: 2px solid var(--color-green);
}

.cv .cv_item_en::after {
    content: "";
    position: absolute;
    width: 22px;
    height: -webkit-fill-available;
    top: 30px;
    left: 0;
    margin-top: -2px;
    border-right: 2px solid var(--color-green);
}

@media screen and (max-width: 900px) {
    .cv .CVcontainer .boxRow {
        flex-direction: column;
    }

    .cv .CVcontainer .boxRow .w_50 {
        width: 100%;
    }
}

/* ------------------------- Books ------------------------- */
.BPBox {
    width: 80%;
    height: 30rem;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    margin: 4rem 5rem;
    overflow: hidden;
}

.BPImg {
    width: 70%;
    height: 100%;
    background-color: var(--color-light-black);
    overflow: hidden;
}

.BPImg img {
    width: 100%;
    height: 100%;
    transition: var(--transition);
}


.BPBody {
    width: 30%;
    height: 100%;
    padding-left: 1rem;
    display: flex;
    gap: 1rem;
    align-items: flex-end;
    justify-content: center;
    flex-direction: column;
}

.BPBody h3, .BPBody p {
    text-align: right;
}

.BPBody p {
    padding-right: .5rem;
}

@media screen and (max-width: 1024px) {
    .BPBox {
        width: 90%;
        height: max-content;
        margin: 4rem 0;
        flex-direction: column;
    }

    .BPBody {
        width: 90%;
        padding: 2.5rem;
        align-items: center;
    }

    .BPBody h3, .BPBody p {
        text-align: center;
    }

    .BPImg {
        width: 98%;
        height: 50vw;
    }
}

@media screen and (max-width: 600px) {
    .BPBoxBody {
        width: 98%;
    }
    
}

/* ------------------------- About Book ------------------------- */
.ABPBox {
    width: 100%;
    height: max-content;
}

.ABPBox_img {
    width: 80%;
    object-fit: contain;
}

/* ------------------------- About Page ------------------------- */
.AC {
    width: auto;
    height: max-content;
    margin: 1rem 7rem;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
    flex-wrap: wrap;
}

.ACImg {
    width: 40%;
    height: 100%;
    overflow: hidden;
}

.ACImg img {
    width: 90%;
    height: 90%;
}

.ACBody {
    width: 60%;
    height: auto;
    padding: 2rem;
    gap: 1rem;
    display: flex;
    flex-direction: column;
}

.ACBodyUl {
    width: 100%;
    height: 11rem;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: row;
    gap:1.4rem;
    margin: 1rem;
    margin-right: 2rem;
}

.ACBodyUl li {
    width: 45%;
    list-style: arabic-indic;
}

@media screen and (max-width: 1024px) {
    .AC {
        flex-direction: column;
    }
    
    .ACImg {
        display: none;
    }
    
    .ACBody {
        width: 90%;
    }
}

@media screen and (max-width: 800px) {    
    .AC {
        margin: 0;
    }

    .ACBodyUl {
        height: auto;
        flex-wrap: nowrap;
    }
}

/* ------------------------- Video ------------------------- */
.aparatVideo {
    background: var(--color-light-black);
}

/* ------------------------- Form ------------------------- */
#form {
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    gap:1.4rem;
    padding: 0 8rem;
    padding-right: 2rem;
}

#form label {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
    color: var(--color-white);
}

#name {
    width: 46%;
}

#email {
    text-align: left;
    width: 100%;
    display: flex;
    align-self: flex-end;
}

#form label input, #form label textarea{
    padding: 10px;
    opacity: .8;
    width: 100%;
    background-color: transparent;
    color: var(--color-white);
    transition: var(--transition);
}

#form label textarea {
    height: auto;
}

.input_error {
    border: 1px solid #d35656;
}

.input_sucsses {
    border: 1px solid var(--color-gray);
}

@media screen and (max-width: 900px) {    
    #contact {
        width: 100%;
        flex-direction: column;
        gap:3rem;
        padding: 0 2rem;
    }

    #contact > .w_25 {
        width: 100%;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-evenly;
        margin-bottom: 5rem;
    }

    #contact > .w_75 {
        width: 100%;
    }

    #form {
        padding: 0;
    }
}
  
@media screen and (max-width: 600px) {
    #name {
        width: 100%;
    }
}